* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}

:root {
  --bg-clr: #151515;
  --frnt-clr: #fff;
  --acnt-clr: #80deea;
  --shadow-clr: hsl(0, 0%, 96%, 0.5);
  --transition: all 900ms cubic-bezier(0.4, 1.1, 0.76, 1.29);
}
body {
  background: #151515;
  background: var(--bg-clr);
  color: #fff;
  color: var(--frnt-clr);
  line-height: 1.5;
  height: 100vh;
  overflow-y: scroll;
}
ul {
  list-style: none;
}
nav {
  position: -webkit-sticky;
  position: sticky;
  top: 0%;
  left: 0%;
  right: 0%;
}
nav ul {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: 10vh;
  grid-gap: 1.3rem;
  gap: 1.3rem;
  padding: 0 10rem 0 3rem;
}
nav ul li {
  padding: 0.32rem 0.83rem;
  cursor: pointer;
  font-weight: 600;
}
nav ul li:hover {
  /* background-color: #eee; */
  color: #151515;
  color: var(--bg-clr);
  text-shadow: 3px 3px 0 hsl(0, 0%, 96%), -1px -1px 0 hsl(0, 0%, 96%),
    1px -1px 0 hsl(0, 0%, 96%), -1px 1px 0 hsl(0, 0%, 96%),
    1px 1px 0 hsl(0, 0%, 96%);
  transition: all 100ms ease-in;
}
nav ul li:first-child {
  font-size: 1.3rem;
  font-weight: 800;
}
nav ul li:nth-child(2) {
  margin-left: auto;
}

.introductionSection {
  height: 90vh;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.intr-img-Container {
  display: grid;
  grid-template-columns: 10rem auto;
  place-items: center;
}
.intr-img-Container p {
  grid-column: 1/2;
  height: 5rem;
  width: -webkit-max-content;
  width: max-content;
  letter-spacing: 0.31rem;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  font-weight: 700;
  color: rgb(97, 97, 97);
}
.intr-img-Container p span {
  color: rgb(255, 255, 255);
}

.intr-img-Container img {
  position: relative;
  grid-column: 2/3;
  margin-top: 1rem;
  left: -4rem;
  width: 100%;
  object-fit: cover;
  height: 79vh;
  border-radius: 5rem;
  border-top-right-radius: 20rem;
}

.intro-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  grid-gap: 0.9rem;
  gap: 0.9rem;
}
.into-fullname {
  position: relative;
  background-color: #fff;
  background-color: var(--frnt-clr);
  color: #151515;
  color: var(--bg-clr);
  z-index: 0;
  cursor: pointer;
}
.into-fullname h1 {
  padding: 0.1em 1em;
  z-index: 1;
  font-size: 2.7rem;
  letter-spacing: 0.8rem;
}
.into-fullname::after {
  z-index: -1;
  content: "";
  position: absolute;
  bottom: 0%;
  top: 0%;
  left: 0%;
  width: 100%;
  -webkit-clip-path: circle(0px at 0 -5%);
          clip-path: circle(0px at 0 -5%);
  background-color: #151515;
  background-color: var(--bg-clr);
  overflow: hidden;
}
.into-fullname::before {
  position: absolute;
  z-index: -2;
  content: "";
  height: 100%;
  width: 100%;
  left: 2%;
  top: 20%;
}
.into-fullname:hover {
  transition: all 900ms cubic-bezier(0.4, 1.1, 0.76, 1.29);
  transition: var(--transition);
  outline: 1px solid #fff;
  color: #fff;
}
.into-fullname:hover::after {
  outline: 1px solid #fff;
  transition: all 900ms cubic-bezier(0.4, 1.1, 0.76, 1.29);
  transition: var(--transition);
  -webkit-clip-path: circle(200% at 0 100px);
          clip-path: circle(200% at 0 100px);
}
.into-fullname:hover::before {
  outline: 1px solid #fff;
  transition: all 300ms cubic-bezier(0.4, 1.1, 0.76, 1.29);
  transition-delay: 0ms;
}
.into-scndline {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 0.9rem;
  gap: 0.9rem;
}
.into-line {
  width: 10rem;
  height: 0.3rem;
  background-color: #fff;
  background-color: var(--frnt-clr);
  border-radius: 0.9rem;
}
.into-explaining {
  font-size: 1.5rem;
  color: #151515;
  color: var(--bg-clr);
  font-weight: 800;
  text-shadow: 3px 3px 0 hsl(0, 0%, 96%, 0.5), -1px -1px 0 hsl(0, 0%, 96%, 0.5),
    1px -1px 0 hsl(0, 0%, 96%, 0.5), -1px 1px 0 hsl(0, 0%, 96%, 0.5),
    1px 1px 0 hsl(0, 0%, 96%, 0.5);
  text-shadow: 3px 3px 0 var(--shadow-clr), -1px -1px 0 var(--shadow-clr),
    1px -1px 0 var(--shadow-clr), -1px 1px 0 var(--shadow-clr),
    1px 1px 0 var(--shadow-clr);
}
.into-paragtaph {
  width: 70%;
  font-size: 0.9rem;
}
.calltoaction {
  width: 12rem;
  height: 3rem;
  margin-top: 2rem;
  background-color: #80deea;
  background-color: var(--acnt-clr);
  cursor: pointer;
  border: none;
}
.calltoaction:hover {
  transition: all 250ms ease-in-out;
  background-color: transparent;
  color: #fff;
  color: var(--frnt-clr);
  border: 1px solid #fff;
  border: 1px solid var(--frnt-clr);
}
.social {
  grid-column: 2/2;
  height: 3rem;
  width: 13rem;
  display: flex;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  margin-left: auto;
}
.social-icons {
  position: relative;
  width: 2.5rem;
  z-index: 2;
  margin: 0 0.41rem;
  cursor: pointer;
}
.socila-icon-container {
  position: relative;
  width: 2.5rem;
  height: 2.5rem;
  z-index: 0;
  display: grid;
  place-content: center;
  border-radius: 50%;
}
.socila-icon-container::before,
.socila-icon-container::after {
  content: "";
  position: absolute;
  top: 0%;
  bottom: 0%;
  left: 0%;
  right: 0%;
  border-radius: 50%;
  opacity: 0;
}

.socila-icon-container:hover::before {
  opacity: 0.81;
  z-index: 1;
  background-color: #151515;
  background-color: var(--bg-clr);
}
.socila-icon-container:hover:after {
  z-index: -1;
  background-color: #fff;
  background-color: var(--frnt-clr);
  -webkit-animation: beating 700ms infinite alternate;
          animation: beating 700ms infinite alternate;
}
@-webkit-keyframes beating {
  0% {
    opacity: 0.3;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100% {
    opacity: 0.91;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}
@keyframes beating {
  0% {
    opacity: 0.3;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100% {
    opacity: 0.91;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}

